import React, { Component } from 'react';
import {Link} from "react-router-dom";
import "../HomeS3.css";

const slideImages = "http://localhost:5000/slideimages"

class Slide1 extends Component {
    constructor(){
        super()

        this.state={
            slideImages1: ''
        }
    }

    renderImages1 = (data) => {
        if(data){
            return data.map((item) => {
                return(
                    
                    <div className="carousel-item">
                            <Link to={"/"}><img src={`${item.url}`} alt=""/></Link>
                    {/* <Link href="#"><img src="img/mobile.jpg" alt=""/></Link> */}
                    </div>
                    
                )
            })
        }
    }

    render() { 
        return ( 
            <div id="slide1" className="carousel slide" data-ride="carousel">
                <div className="carousel-inner">
                    <div className="carousel-item active">
                        <img src="https://i.ibb.co/VQZmKwJ/slide5.jpg" alt=""/>
                    </div>
                    <ul className="carousel-indicators">
                        <li data-target="#slide1" data-slide-to="0" className="active"></li>
                        <li data-target="#slide1" data-slide-to="1"></li>
                        <li data-target="#slide1" data-slide-to="2"></li>
                        <li data-target="#slide1" data-slide-to="3"></li>
                        <li data-target="#slide1" data-slide-to="4"></li>
                        <li data-target="#slide1" data-slide-to="5"></li>
                        <li data-target="#slide1" data-slide-to="6"></li>
                        <li data-target="#slide1" data-slide-to="7"></li>
                        <li data-target="#slide1" data-slide-to="8"></li>
                        <li data-target="#slide1" data-slide-to="9"></li>
                        <li data-target="#slide1" data-slide-to="10"></li>
                        <li data-target="#slide1" data-slide-to="11"></li>
                    </ul>
                    <div className="carousel-item">

                    </div>
                    {this.renderImages1(this.state.slideImages1)}
                    
                </div>
            </div>   
        );


}

    componentDidMount(){
        fetch(slideImages,{method: "GET"})
        .then((res) => res.json())
        .then((data) => {
            this.setState({slideImages1:data})
            // console.log(data)
        })
    }

}

export default Slide1;