import React, { Component } from 'react';
import axios from "axios";
import {Link} from "react-router-dom";
import "./productDetails.css";

const url = "http://localhost:5000/list/productDetails";

class ProductDetails extends Component {
    constructor(){
        super()

        this.state = { 
            product: ""
        }
    }

    onBack = () => {
        let productId = sessionStorage.getItem("type")
        this.props.history.push(`/list/${productId}`)
    }
    
    render() { 
        // console.log(this.state.product)
        // console.log(this.props)
        return ( 
            <div className="card mb-3 details-card">
                <div className="row no-gutters">
                    <div className="col-md-5 card-img">
                        <img src={this.state.product.thumb} className="card-img" alt="..."/>
                    </div>
                    <div className="col-md-7 card-detail">
                        <div className="card-body">
                            <h5 className="card-title"><strong>Brand : </strong>{this.state.product.brand}</h5>
                            <h5 className="card-title"><strong>Description : </strong>{this.state.product.description}</h5>
                            <h5 className="card-title"><strong>Cost : </strong>{this.state.product.cost}</h5>
                            <h5 className="card-title"><strong>Rating : </strong><ion-icon name="star"></ion-icon><ion-icon name="star"></ion-icon><ion-icon name="star"></ion-icon><ion-icon name="star"></ion-icon><ion-icon name="star-outline"></ion-icon></h5>
                            <h5 className="card-title"><strong>Fabric : </strong>{this.state.product.fabric}</h5>
                            <h5 className="card-title"><strong>Size : </strong>{this.state.product.size}</h5>
                            <p className="card-text">Lorem ipsum dolor sit amet consectetur adipisicing elit. Provident sunt quam neque totam praesentium mollitia. Possimus sint, velit esse hic autem officia aliquid doloremque exercitationem!</p>
                            <p className="card-text"><small className="text-muted">Last updated 1 day ago</small></p>
                            <Link href="#" className="btn btn-primary mt-5" onClick={this.onBack}>Go Back</Link>
                            <Link to={`/order/${this.props.match.params.id}`} href="#" className="btn btn-success ml-5 mt-5">Buy Now</Link>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    componentDidMount(){
        let productid = this.props.match.params.id;
        axios.get(`${url}/${productid}`)
        .then((response) => {
            this.setState({product:response.data[0]})
        })
    }
}

export default ProductDetails;