import React, { Component } from 'react';
import {Link} from "react-router-dom";
import "../HomeS3.css";

const slideImages2 = "http://localhost:5000/slideimages2"


class Slide2 extends Component {
    constructor(){
        super()

        this.state={

            slideImages2: ''
            
        }
    }


    renderImages2 = (data) => {
        if(data){
            return data.map((item) => {
                return(
                    
                    <div className="carousel-item">
                            <Link to={"/"}><img src={`${item.url}`} alt=""/></Link>
                    {/* <Link href="#"><img src="img/mobile.jpg" alt=""/></Link> */}
                    </div>
                    
                )
            })
        }
    }


    render() { 
        return ( 
                <div id="slide2" className="carousel slide" data-ride="carousel">
                <div className="carousel-inner">
                    <div className="carousel-item active">
                    <img src="https://i.ibb.co/SB5CqvN/slide20.jpg" alt=""/>
                    </div>
                    <ul className="carousel-indicators">
                    <li data-target="#slide2" data-slide-to="0" className="active"></li>
                    <li data-target="#slide2" data-slide-to="1"></li>
                    <li data-target="#slide2" data-slide-to="2"></li>
                    <li data-target="#slide2" data-slide-to="3"></li>
                    <li data-target="#slide2" data-slide-to="4"></li>
                    <li data-target="#slide2" data-slide-to="5"></li>
                    <li data-target="#slide2" data-slide-to="6"></li>
                    <li data-target="#slide2" data-slide-to="7"></li>
                    <li data-target="#slide2" data-slide-to="8"></li>
                    <li data-target="#slide2" data-slide-to="9"></li>
                    <li data-target="#slide2" data-slide-to="10"></li>
                    <li data-target="#slide2" data-slide-to="11"></li>
                    </ul>
                    <div className="carousel-item">
                
                    </div>
                    {this.renderImages2(this.state.slideImages2)}
                    
                </div>
                </div>   
        );


}

    

    componentDidMount(){
        fetch(slideImages2,{method: "GET"})
        .then((res) => res.json())
        .then((data) => {
            this.setState({slideImages2:data})
            // console.log(data)
        })
    }

}



export default Slide2;