import React, { Component } from 'react';
import {Link} from "react-router-dom";
import "./HomeS1.css";

const listUrl = "http://localhost:5000/list"


class Home extends Component {
    constructor(){
        super()

        this.state={
            listItems: '',
        }
    }

    renderList = (data) => {
        if(data){
            return data.map((item) => {
                return(                   
                        <li>
                            <Link to={`/list/${item.code}`} value={item._id} className="nav-link" role="button"  aria-haspopup="true" aria-expanded="false">
                            {item.name}
                            </Link>      
                        </li>  
                )
            })
        }
    }

    render() { 
        // console.log(this.state.listItems)
        return ( 
                <nav className="navbar navbar-expand-md navbar2">
                    <div className="container-expand-lg">
                        <div className="row">
                            <div className="col">
                                <ul className="navbar-nav">
                                    <li className="nav-item">
                                                        
                                    </li>
                                    {this.renderList(this.state.listItems)}
                                </ul>
                            </div>            
                        </div>
                    </div>
                </nav>
        );
    }

    componentDidMount(){
        fetch(listUrl,{method: "GET"})
        .then((res) => res.json())
        .then((data) => {
            this.setState({listItems:data})
            // console.log(data)
        })
    }

    
}

export default Home;