import React, { Component } from 'react';
import {Link} from "react-router-dom";
import "./productListing.css";  

const ProdcutListingDisplay = (props) => {

    const renderProduct = ({productData}) => {
        if(productData){
            return productData.map((item) => {
                return ( 
                    <div className="container card-bg">
                        <div className="col-md-3 cards">
                            <Link to={`/product/${item._id}`}>
                                <div className="card" >
                                    <img className="card-img-top" src={`${item.thumb}`} alt="Card image cap"/>
                                    <div className="card-body">
                                        <h5 className="card-title">{`${item.brand}`}</h5>
                                        <p className="card-text">{`${item.description}`}</p>
                                    </div>
                                    <div className="card-body">
                                        <h5 className="card-title">{`Fabric: ${item.fabric}`}</h5>
                                        <p className="card-text">{`Rs: ${Number(item.cost)}`}</p>
                                    </div>
                                    <div className="card-body">
                                        <Link to={`/`} className="btn btn-primary">Add To Kart</Link>
                                        <Link to={`/order/${item._id}`} className="btn btn-success ml-5">Buy Now</Link>
                                    </div>
                                </div>
                            </Link>
                        </div>
                    </div>
                )
            }) 
        }else {
            return(
                <div className="loader">
                    <img src="../img/loader.gif" alt="loaderImage"/>
                </div>
            )
        }
    }

    return(
        <div>
            
                {renderProduct(props)}
            
        </div>
        
    )
}


export default ProdcutListingDisplay;





