import React, { Component } from 'react';
import "./HomeS2.css";
import {Link} from "react-router-dom";
import { Grid, Row, Col } from "react-bootstrap";

const productSymbols = "http://localhost:5000/productsymbols"

class HomeS2 extends Component {
    constructor(){
        super()

        this.state={
            productSymbols: ''
        }
    }

    renderProductSymbols = (data) => {
        if(data){
            return data.map((item) => {
                return(
                    
                    <div className="img-box ml-md-5">
                            <Link href="#"><img src={`${item.url}`} alt=""/></Link>
                    {/* <Link href="#"><img src="img/mobile.jpg" alt=""/></Link> */}
                    </div>
                    
                )
            })
        }
    }

    // renderProductName = (data) => {
    //     if(data){
    //         return data.map((item) => {
    //             return (
    //                     <div className="row ml-sm-5">
    //                         <Link href="#">{`${item.name}`}</Link>
    //                     </div>
    //             )
    //         })
    //     }
    // }

    render() { 
        return (

            <section className="navbar3">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col">
                            <div className="row ml-sm-5">
                                <div className="col img-box">
                                
                                </div>
                                {this.renderProductSymbols(this.state.productSymbols)}
                                <div className="col ml-sm-2">
                                {/* <Link href="#">Mobile's</Link> */}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        
        );
    }

    componentDidMount(){
        fetch(productSymbols,{method: "GET"})
        .then((res) => res.json())
        .then((data) => {
            this.setState({productSymbols:data})
            // console.log(data)
        })
    }
}

export default HomeS2;