import React from "react";
import {BrowserRouter, Route} from "react-router-dom";
import Header from "../Header";
import Home from "./HomePage/Home";
import Login from "./LoginPage/Login";
import Signup from "./SignupPage/Signup";
import Notification from "./NotificationPage/Notification";
import Kart from "./KartPage/Kart";
import Listing from "./ProductListingPage/productListingApi";
import ProductDetails from "./ProductDetailsPage/productDetails";
import Form from "./Order/form";
import Orders from "./Order/orderApi";


const Routing = () => {
    return(
        <BrowserRouter>
            <div>
                <Header/>
                <Route exact path="/" component={Home}/>
                <Route exact path="/login" component={Login}/>
                <Route exact path="/signup" component={Signup}/>
                <Route exact path="/notification" component={Notification}/>
                <Route exact path="/kart" component={Kart}/>
                <Route exact path="/list/:id" component={Listing}/>
                <Route exact path="/product/:id" component={ProductDetails}/>
                <Route exact path="/order/:id" component={Form}/>
                <Route exact path="/vieworder" component={Orders}/>
            </div>
        </BrowserRouter>
    )
}

export default Routing;