import React, { Component } from 'react';
import {Link} from "react-router-dom";
import "./Header.css";


class Header extends Component {
    render() { 
        return ( 
            <nav className="navbar navbar-expand-md sticky-top navbar1 navbar-light"> 
                <div className="container-fluid">
                <Link className="navbar-brand" href="#" to = {`/`}> <img src="https://i.ibb.co/Zcn0Fps/logo.png" alt="brand logo"/></Link>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarResponsive">
                    <span className="navbar-toggler-icon"></span>
                </button>   
                <div className="collapse navbar-collapse" id="navbarResponsive">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item mt-sm-4">
                            <h1 id="time"></h1>
                        </li>
                        <li className="nav-item ml-sm-3 mr-sm-4 notification">
                            <Link className="nav-link" to={"/notification"}><ion-icon name="notifications-outline"></ion-icon></Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link active" to={"/login"}><strong>LogIn</strong></Link>
                        </li>
                        <li className="nav-item ml-sm-3">
                            <Link to={"/signup"}>SignUp</Link>
                        </li>
                        <li className="nav-item ml-sm-3 mr-sm-5">
                            <Link className="nav-link" to={"/vieworder"}><ion-icon name="cart-outline"></ion-icon></Link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
        );
    }
}

export default Header;