import React, { Component } from 'react';
import axios from "axios";
import "../filters/filter.css";
import ProdcutListingDisplay from "./productListingDisplay"
import CostFilter from "../filters/costFilter";
import CuisineFilter from "../filters/pantShirt";

const productUrl = "http://localhost:5000/list/product?code=";

class Listing extends Component {
    constructor(props){
        super(props)

        this.state={
            productListing: ""
        }
    }

    setDataPerFilter(sortedData){
        this.setState({productListing:sortedData})
    }

    render() { 
        // console.log(this.props)
        return ( 
            <div>
                <div className="row">
                    <div className="container col-md-2">
                        <CostFilter datapercost={(data) => {this.setDataPerFilter(data)}}/>
                    </div>
                    <div className="container col-md-10">
                        <ProdcutListingDisplay productData = {this.state.productListing}/>
                    </div>
                </div>
            </div>
        );
    }

    componentDidMount(){
        let productId = this.props.match.params.id;
        sessionStorage.setItem("type",productId);
        axios.get(`${productUrl}${productId}`)
        .then((response) => {
            this.setState({productListing:response.data})
        })
    }
}

export default Listing;